.header_content{
      width:  100%;
      height: 365px;
      background: var(--dark-200);
      position: relative;
      overflow: hidden;
      border-bottom: solid 5px var(--theme);
}

.header_banner{
      width: 100%;
      height: 100%;
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;
      top: 0;
      left: 0;
}

.title_banner{
      position: relative;
      width: 400px;
      height: 100%;
      background:  rgba(255,255,255,.3);
      -webkit-backdrop-filter: blur(10px);
      -moz-backdrop-filter: blur(10px);
      backdrop-filter: blur(10px);
      margin: 0 auto;
      display: flex;
      align-items: center;
      justify-content: center;
}

.title_banner h1{
      text-transform: uppercase;
      color: var(--light);
      font-size: 1.4rem;
      letter-spacing: 4px;
      font-weight: 400;
      padding: 0 2rem;
      text-align: center;
}