.friends_aside{
      width: 252px;
      height: auto;
      min-height: 100%;
      background: var(--dark-200);
      flex-shrink: 0;
      position: relative;
      z-index: 2;
}

.friends_header{
      height: 60px;
      position: relative;
      width: 100%;
}

.friends_header h2{
      width: 100%;
      height: 100%;
      color: var(--light);
      text-transform:  uppercase;
      font-weight: 400;
      font-size: 1.4rem;
      display: flex;
      align-items: center;
      padding-left: 2rem;
      letter-spacing: 5px;
}

.friends_header p{
      width: 28px;
      height: 35px;
      background: var(--theme);
      color: var(--light);
      display: flex;
      align-items: center;
      justify-content: center;
      top: 0;
      right: 8rem;
      position: absolute;
}

.friends_header span,
.friends_header span i{
      width: 50px;
      height: 50px;
      background: var(--light);
      display: flex;
      align-items: center;
      justify-content: center;
      color: var(--dark-200);
}

.friends_header span{
      position: absolute;
      top: 0;
      right: -40px;
}

.friends_header span *{
      font-size: 2rem;
}

.friends_list{
      width: 100%;
}

.friends_wrapper{
      width: 100%;
      display: flex;
      flex-direction: column;
      gap: .1rem;
}

.friends_wrapper li,
.friends_wrapper li a{
      width: 100%;
      height: 60px;
}

.friends_wrapper li{
      position: relative;
}

.friends_wrapper li:before{
      width: 0;
      height: 100%;
      background: var(--light);
      top: 0;
      left: 0;
      transition: .3s ease-out;
}

.friends_wrapper li:hover:before{
      width: 262px;
}

.friends_wrapper li a{
      display: flex;
      gap: 1rem;
      background: var(--dark-100);
      box-shadow: 0 0 var(--light);
      transition: .3s ease-out;
      position:  relative;
      background: none;
}

.friends_avatar{
      width: 60px;
      height: 60px;
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;
      flex-shrink: 0;
}

.friends_details{
      flex-grow: 1;
      padding: .5rem 0;
      height: 100%;
      overflow: hidden;padding-right: 1rem;
}

.friends_details h3{
      color: var(--grys-100);
      text-transform: uppercase;
      font-size: 1.2rem;
      font-weight: 400;
      letter-spacing: 3px;
}

.friends_wrapper li a:hover h3{
      color:var(--dark-100);
}

.friends_details p{
      color: var(--grys-400);
}