.wrapper_content{
      width: 290px;
      height: 100%;
      min-height: 800px;
      background: var(--grys-300);
      display: flex;
}

.social_menu{
      width: 75px;
      height: 100%;
      min-height: 800px;
      background: var(--theme);
      display: flex;
      justify-content: center;
      flex-shrink: 0;
}

.social_menu__content{
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 15rem;
      gap: 1rem;
}

.social_menu__content a,
.social_menu__content a i{
      width:40px;
      height:40px;
      display: flex;
      align-items: center;
      justify-content:center;
}

.social_menu__content a{
      color: var(--light);
      transition: .2s;
}

.social_menu__content a *{
      font-size: 2rem;
}

.social_menu__content a:hover{
      color: var(--dark-200);
}

.user_content{
      width: 215px;
      flex-grow: 1;
      height: 100%;
}

.wrapper_user{
      position: relative;
      width: 100%;
      height: auto;
}

.user_avatar{
      width: 215px;
      height: 215px;
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;
}

.user_details{
      width: 100%;
      height: 70px;
      display: flex;
      align-items: center;
      justify-content: center;
      background: var(--dark-200);
      color: var(--light);
      position: relative;
}

.user_details h3{
      font-size: 1.4rem;
      font-weight: 400;
      letter-spacing: .4rem;
      text-transform: uppercase;
}

.user_details span{
      position: absolute;
      background: var(--theme);
      font-size: 1.6rem;
      letter-spacing: .4em;
      padding: .5rem 1rem;
      right: 1rem;
      bottom: -50%;
      transform: translateY(-50%);
      color: var(--light);
}

.wrapper_relation{
      margin-top: 5rem;
      width: calc(100% - 2rem);
      margin-left: 1rem;
      position: relative;
}

.wrapper_relation:before{
      width: 1px;
      height: 100%;
      background:var(--theme);
      top: 0;
      right: 0;
}

.relation_avatar{
      width: 185px;
      height: 185px;
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;
}

.relation_crush{
      position: absolute;
      top: 0;
      left: 1rem;
      background: var(--dark-200);
      color: var(--light);
      text-transform: uppercase;
      transform: translateY(-50%);
      padding: .5rem 1rem;
      font-size: 1.2rem;
      letter-spacing: .4rem;
}

.relation_name{
      position: absolute;
      top: 185px;
      right: 2rem;
      background: var(--theme);
      color: var(--light);
      text-transform: uppercase;
      padding: .5rem 1rem;
      font-size: 1.2rem;
      letter-spacing: .4rem;
      transform: translateY(-50%);
}

.relation_details{
      margin-top: 5rem;
}

.title{
      position: relative;
      padding: .5rem 1rem;
      border: solid 1px var(--dark-100);
      display: flex;
      align-items: center;
      justify-content: center;
      text-transform: uppercase;
}

.title span{
      font-size: 1.4rem;
      letter-spacing: .4rem;
}

.title:before{
      width: 40px;
      height: 5px;
      top: -5px;
      left: 1rem;
}

.title.aside{
      width: 110px;
}

.title.aside:before{
      background: var(--grys-300);
}

.relation_description{
      margin: 3rem 0;
      width: calc(100% - 1rem);
}

.relation_description p{
      font-size: 1.2rem;
}

.relation_details a{
      width: 80px;
      line-height: 32px;
      text-align: center;
      text-transform: uppercase;
      background: var(--theme);
      color: var(--light);
      margin-left: calc(100% - 80px);
      letter-spacing: .4rem;
}