@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700&display=swap');

:root{
  --ft-1: 'Poppins', sans-serif;

  --grys-400: #818181;
  --grys-300: #eee;
  --grys-200: #ebebeb;
  --grys-100: #f7f7f7;
  --dark-200: #1e1e1e;
  --dark-100:  #222;
  --light: #fff;
  --theme: #ec555a;

  /*globals */
  font-size: 62.5%;
  font-family: var(--ft-1);
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:before, 
:after{
  content: '';
  position: absolute;
}

body{
  min-height: 100vh;
  overflow-x: hidden;
  background: var(--grys-100);
}

body * {
  font-size: 1rem;
}

a, span{
  display: inline-block;
}

a{
  text-decoration: none;
}

ul{
  list-style: none;
}

img{
  width: 100%;
  object-fit: cover;
}