.wrapper_load{
      width: 100%;
      height: 100vh;
      position: fixed;
      top: 0;
      left: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      z-index: 999;
      background: var(--theme);
} 

.load_inner{
      display: flex;
}

.load_inner h1{
      display: flex;
      align-items: center;
      color: var(--light);
      gap: .5rem;
      letter-spacing: .5rem;
}

.load_inner h1 span{
      font-size: 1.4em;
      text-transform: uppercase;
      position: relative;
      font-weight: 400;
      opacity: 0;
      animation: pulse 2s ease-in-out infinite;
}

@keyframes pulse{
      0%{
            opacity: 0;
      }
      15%{
            opacity: 1;
      }
      30%{
            opacity: 0;
      }
      100%{
            opacity: 0;
      }
}