.section_user{
      flex-grow: 1;
      height: auto;
      background: var(--light);
      margin-top: 8rem;
      padding: 2rem 1.5rem;
      display: flex;
      flex-direction: column;
}

.data_user{
      padding-bottom: 2.5rem;
      border-bottom: solid 1px var(--grys-100);
}

.title.section{
      width: 130px;
}

.title.section:before{
      background: var(--light);
}

.user_about{
      margin-top: 2rem;
}

.user_about p{
      font-size: 1.4rem;
}

.wrapper_video{
      margin-top: 2rem;
}

.video_title{
      width: 100%;
      display: flex;
      justify-content: center;
}

.video_title h4{
      text-align: center;
      text-transform: uppercase;
      letter-spacing:.2rem;
      font-weight: 400;
      font-size: 1.2rem;
}

.video_box{
      height: 285px;
      width: 505px;
      background: var(--dark-200);
      margin: 2rem 0;
      box-shadow: 0 5px var(--grys-300);
}

.video_box iframe{
      border: none;
}